.centerInfo_cardioCenter, .centerInfo_traumaCenter {
    width: 100%;
    height: 604px;
    background-image: url("../img/cardioBackTwo.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
}
.centerInfo_traumaCenter {background-image: url("../img/traumaBackTwo.png");}
.textAboutCenter h6 {
    font-family: "TTHoves-ExtraBold";
    font-weight: 800;
    font-size: 44px;
    line-height: 52px;
    text-transform: uppercase;
    color: #F9F9F9;
    margin-top: 70px;
    margin-left: 50px;
}
.textAboutCenter p {
    width: 528px;
    font-family: "TTHoves-DemiBold";
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    color: #F9F9F9;
    margin-top: 50px;
    margin-left: 50px;
}